<template>
    <div class="x-messenger fill-height">
        <v-window v-model="schema.window" class="fill-height">
            <v-window-item class="x-win-user-list fill-height">
                <user-list v-bind="$props" v-on="$listeners"></user-list>
            </v-window-item>

            <v-window-item class="x-win-user-chat fill-height">
                <user-chat v-bind="$props" v-on="$listeners"></user-chat>
            </v-window-item>
        </v-window>
    </div>
</template>

<script>
import Vue from 'vue';
import UserList from './UserList.vue';
import UserChat from './UserChat.vue';

export default {
    name: 'Chat',
    components: {
        UserList,
        UserChat
    },
    props: {
        schema: {
            type: [Object]
        },
    },
    computed: {

    },
    data() {
        return {
            selectedRow: 0,
        }
    },
    methods: {
        addUserProperty() {
            if (!this.schema.user) {
                Vue.set(this.schema, 'user', undefined);
            }
            this.selectUser();
        },

        selectedUser(user) {
            this.schema.user = user;
            if (this.$listeners && this.$listeners['selected-user']) {
                return this.$emit('selected-user', user);
            }
            this.schema.window = 1;
        },

        unselectedUser() {
            if (this.$listeners && this.$listeners['unselected-user']) {
                return this.$emit('unselected-user');
            }
            this.schema.window = 0;
        },

        selectRow() {
            if (this.schema.user && this.schema.users && this.schema.users.length > 0) {
                this.selectedRow = this.schema.users.findIndex(user => user.id === this.schema.user.id);
                this.schema.user = this.schema.users[this.selectedRow];
            }
        },

        selectUser() {
            if (this.schema && this.schema.users && this.schema.users.length > 0 && (!this.schema.user || Object.keys(this.schema.user).length <= 0)) {
                this.schema.user = this.schema.users[this.selectedRow];
            }
            this.selectRow();
        },

        sendMessage(fields) {
            this.$emit('send-message', fields);
        },
    },
    created() {
        this.addUserProperty();
    },
}
</script>

<style lang="sass" scoped>
.x-messenger
    .x-win-user-list,
    .x-win-user-chat
        border-left: 0px solid #cccccc
        overflow-y: auto

.theme--dark
    .x-win-user-chat
        border-left: 1px solid #333333
</style>
