<template>
    <div class="x-user-list fill-height">
        <v-list subheader two-line dense>
            <v-list-item-group v-model="$parent.$parent.$parent.selectedRow" color="primary">
                <template v-for="(user, $index) in schema.users">
                    <v-list-item v-if="$index <= schema.users.length" :key="$index" @click="$parent.$parent.$parent.selectedUser(user)" class="x-list-item">
                        <div class="x-avatar mr-2">
                            <v-badge dot bordered bottom offset-x="10" offset-y="10" :color="status(user)">
                                <v-avatar size="36">
                                    <v-img :src="user.avatar" :alt="user.name"></v-img>
                                </v-avatar>
                            </v-badge>
                        </div>

                        <v-list-item-content class="py-0">
                            <v-list-item-title v-text="user.name"></v-list-item-title>
                            <v-list-item-subtitle v-text="user.profile"></v-list-item-subtitle>
                        </v-list-item-content>

                        <v-list-item-action class="my-0">
                            <v-icon color="grey lighten-1">mdi-chevron-right-circle</v-icon>
                        </v-list-item-action>
                    </v-list-item>

                    <v-divider v-if="$index < schema.users.length" :key="$index+1000"></v-divider>
                </template>
            </v-list-item-group>
        </v-list>
    </div>
</template>

<script>
export default {
    name: 'UserList',
    props: {
        schema: {
            type: Object
        },
    },
    methods: {
        status(user) {
            switch (user.status) {
                case 'on':
                    return 'warning';
                case 'busy':
                    return 'error';
                default:
                    return 'white';
            }
        }
    },
}
</script>

<style lang="sass" scoped>
.x-user-list
    .x-list-item
        min-height: 58px
</style>
