<template>
    <div class="x-user-chat six" v-resize="onResize" :style="[{ 'height': `${height -35}px` }]">
        <div class="x-user-panel tree">
            <div class="d-flex align-center">
                <div @click="$parent.$parent.$parent.unselectedUser()" class="x-left-btn d-flex">
                    <v-icon size="34">mdi-chevron-left</v-icon>
                </div>

                <div class="ml-2">
                    <div> {{ schema.user.name }} </div>
                    <div class="text-caption text--disabled"> {{ schema.user.profile }} </div>
                </div>

                <v-spacer></v-spacer>

                <div class="d-flex align-center pa-1">
                    <v-avatar size="38">
                        <img :src="schema.user.avatar" :alt="schema.user.name">
                    </v-avatar>
                </div>
            </div>
        </div>

        <div class="x-message-panel" :style="[{ 'height': `${height - 134}px` }]">
            <div v-for="(msg, $index) in schema.user.messages" :key="$index" class="x-message-items x-right my-2 pa-1 text-body-2">
                <x-user-chat-msg :msg="msg"></x-user-chat-msg>
            </div>
        </div>

        <div class="x-action-panel">
            <v-form @submit.prevent="sendMessage()" v-model="schema.fields.valid" class="x-send-form">
                <v-text-field type="text"
                    v-model="schema.fields.text.value"
                    @click:append="sendMessage()"
                    append-icon="mdi-send"
                    placeholder="Mensagem..."
                    :rules="schema.fields.text.rules"
                    hide-details
                    dense
                    filled
                    required>
                </v-text-field>
            </v-form>
        </div>
    </div>
</template>

<script>
import XUserChatMsg from './UserChatMsg.vue';

export default {
    name: 'UserChat',
    components: {
        XUserChatMsg
    },
    props: {
        schema: {
            type: Object
        },
    },
    data() {
        return {
            height: '100%',
        }
    },
    methods: {
        onResize() {
            this.height = window.innerHeight;
        },
        async sendMessage() {
            this.$parent.$parent.$parent.sendMessage(this.schema.fields);
        },
    },
}
</script>

